<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="customerListChange" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="公司鉴定单据号" size="mini">
            <el-input v-model="params.company_appraisal_sn" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="关联返胎入库单号" size="mini">
            <el-input v-model="params.instorage_goods_sn" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>

          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="公司鉴定结果" size="mini">
            <el-select clearable v-model="params.company_status">
              <el-option label="待厂检" :value="1">待厂检</el-option>
              <el-option label="理赔" :value="2">理赔</el-option>
              <el-option label="不理赔" :value="3">不理赔</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="厂家鉴定结果" size="mini">
            <el-select clearable v-model="params.factory_status">
              <el-option label="理赔" :value="1">理赔</el-option>
              <el-option label="不理赔" :value="2">不理赔</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select v-model="params.auditor_status" clearable>
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label="病象" size="mini">
            <el-input v-model="params.symptom_name_str" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>
          <el-form-item label size="mini">
            <el-checkbox style="margin-left:110px;" false-label="0" true-label="1" v-model="params.aftersales_status" name="type">照顾理赔</el-checkbox>
          </el-form-item>
          <el-form-item size="mini">
            <el-checkbox v-model="params.is_zero" false-label="0" true-label="1" name="type" style="margin:0px 22px 0 62px">打印次数为0</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="salesAddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button @click="audit_enable" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="AUDIT_GET" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="AUDIT_LIST" size="mini" v-if="authority.b_invalid==1">作废</el-button>
        <el-button @click="downLoadFile" plain icon="el-icon-download" size="mini" v-if="authority.b_export_file==1">下载模板</el-button>
        <el-button plain icon="iconfont icon-daoru5" v-if="authority.b_import==1" @click="dialogEmployee" class="addBtn" size="mini">期初导入</el-button>
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button plain size="mini" icon="el-icon-printer" @click="printF">批量打印</el-button>
      </div>

      <xTable :ajax='{ url: "getCompanyAppraisalList", params: params }' ref='xTable' :tableColumn='tableColumn' />
      <upload :options="options" @receiveUploadMsg="onSearch(1)" ref="upload"></upload>
    </div>
    <div>
      <printcompanyjudgement ref="lodopDelivery" @Search="onSearch"></printcompanyjudgement>
    </div>
  </div>
</template>
<script>
import printcompanyjudgement from "common/components/public/printcompanyjudgement.vue"; //出库单打印组件
import { TableHead, paramsModule } from "./config/company_Index";
export default {
  name: "aftersaleCompanyIndex",
  mixins: [TableHead],
  data() {
    return {
      options: {
        title: "期初导入",
        url: "/v2/aftersale/CompanyAppraisal/importCompanyAppraisal"
      },
      hasAuditorStatusPrint: null,
      timeValue1: [],
      goodsList: [], //商品下拉
      departments: {
        options: []
      },
      loading:false,
      SelesTomer: {
        options: []
      }, //客户列表
      dialogFormVisible: false,
      list: [],
      params: this.$publicFution.deepCopy(paramsModule)
    };
  },
  components: {
    printcompanyjudgement
  },
  methods: {
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportCompanyAppraisalUrl(s).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    printF() {
      let companyFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "warning"
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning"
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach(item => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status != 2) {
              errorGoodsArr.push(item.company_appraisal_sn);
              flag = false;
            }
          }
        });
        if (flag) {
          let arryId = this.multipleSelection.map(item => {
            return item.company_appraisal_goods_id;
          });
          this.$refs.lodopDelivery.printPdf(arryId);
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
            <div style="color:#e6a23c">
              ${str.join("，")}
            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true
            }
          );
        }
      }
    },
    downLoadFile() {
      // 下载模板
      this.$axMethod.downloadFileJD().then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
    },
    audit_enable() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].company_appraisal_goods_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auditorPassCompanyAppraisal({
          company_appraisal_goods_id: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg ? res.data.msg : res.data.data,
              type: "success"
            });
            this.onSearch()
          }
        });
      //审核
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].company_appraisal_goods_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auidtorNoPassCompanyAppraisal({
          company_appraisal_goods_id: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg ? res.data.msg : res.data.data,
              type: "success"
            });
            this.onSearch();
          }
        });
      //弃审
    },
    AUDIT_LIST() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].company_appraisal_goods_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auditorInvalidCompanyAppraisal({
          company_appraisal_goods_id: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg ? res.data.msg : res.data.data,
              type: "success"
            });
            this.onSearch()
          }
        });
    },
    seeDetail(row) {
      this.$router.push({
        path: "/aftersale/companyjudgement/company_examine",
        query: {
          id: row.company_appraisal_goods_id
        }
      });
    },
    salesModifyF(row) {
      //修改
      this.$router.push({
        path: "/aftersale/companyjudgement/company_modification",
        query: {
          id: row.company_appraisal_goods_id
        }
      });
    },
    saleschangeF(row) {
      //数据变更
      this.$router.push({
        path: "/aftersale/companyjudgement/company_change",
        query: {
          id: row.company_appraisal_goods_id
        }
      });
    },
    salesbgF(row) {
      //三包照顾
      this.$router.push({
        path: "/aftersale/companyjudgement/company_bg",
        query: {
          id: row.company_appraisal_goods_id
        }
      });
    },
    care() {
      //三包照顾
      let This = this;
      let arryString = [];
      this.multipleSelection.forEach(item => {
        arryString.push(item.company_appraisal_goods_id);
      });
      let airgument = {
        company_appraisal_goods_id: arryString.toString()
      };
      this.$axMethod.CareCompanyAppraisal(airgument).then(res => {
        if (res.data.code == 200) {
          This.$message({
            message: "三包照顾设置成功",
            type: "success"
          });
          this.onSearch()
        }
      });
    },
    salesAddF() {
      this.$router.push({
        path: "/aftersale/companyjudgement/company_Add"
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.timeValue = null;
      this.timeValue1 = null;
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.onSearch(1);
    },
    onSearch(number) {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      this.$refs.xTable._getData(number);
    },
    //删除
    deletes() {
      var deledata = {
        deleID: "company_appraisal_goods_id",
        deleSelect: this.multipleSelection,
        deleApi: "delCompanyAppraisalGs",
        list: {
          UrlApi: "getCompanyAppraisalList",
          data: this.TableData,
          esData: Object.assign({}, this.params)
        }
      };
      this.$common.deletes(deledata, "删除", this, this.onSearch);
    }
  },
  created() {
    this.hasAuditorStatusPrint = window.localStorage.hasAuditorStatusPrint;
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>

<style scoped>
